import React, { useState, useEffect, createContext, useContext, useRef, useCallback } from "react";
import "./App.css";
import axios from "axios";

// Enhanced Google Maps Component with better initialization
const SimpleGoogleMap = ({ latitude, longitude, onLocationSelect, showCurrentLocation = true, repLocation = null, showBothLocations = false }) => {
  const mapRef = useRef(null);
  const [currentLocation, setCurrentLocation] = useState(null);
  const [selectedLocation, setSelectedLocation] = useState(null);
  const [map, setMap] = useState(null);
  const [draggableMarker, setDraggableMarker] = useState(null);
  const [isLoadingLocation, setIsLoadingLocation] = useState(false);
  const [isMapReady, setIsMapReady] = useState(false);

  // Enhanced Google Maps loader with better error handling
  const checkGoogleMapsLoaded = () => {
    return window.google && 
           window.google.maps && 
           window.google.maps.Map && 
           window.google.maps.Marker;
  };

  // Force load Google Maps if not available
  const loadGoogleMapsScript = () => {
    return new Promise((resolve, reject) => {
      if (checkGoogleMapsLoaded()) {
        resolve();
        return;
      }

      // Check if script already exists
      const existingScript = document.querySelector('script[src*="maps.googleapis.com"]');
      if (existingScript) {
        // Wait for existing script to load
        existingScript.addEventListener('load', () => {
          if (checkGoogleMapsLoaded()) {
            resolve();
          } else {
            setTimeout(() => {
              if (checkGoogleMapsLoaded()) {
                resolve();
              } else {
                reject(new Error('Google Maps failed to load'));
              }
            }, 2000);
          }
        });
        existingScript.addEventListener('error', reject);
        return;
      }

      // Create new script
      const script = document.createElement('script');
      script.src = `https://maps.googleapis.com/maps/api/js?key=${window.GOOGLE_MAPS_API_KEY || 'AIzaSyDzxZjDxPdcrnGKb66mT5BIvQzQWcnLp70'}&libraries=places`;
      script.async = true;
      script.defer = true;
      
      script.addEventListener('load', () => {
        if (checkGoogleMapsLoaded()) {
          resolve();
        } else {
          setTimeout(() => {
            if (checkGoogleMapsLoaded()) {
              resolve();
            } else {
              reject(new Error('Google Maps API loaded but components not available'));
            }
          }, 1000);
        }
      });
      
      script.addEventListener('error', reject);
      document.head.appendChild(script);
    });
  };

  // Enhanced geolocation with higher accuracy
  const getHighAccuracyLocation = () => {
    return new Promise((resolve, reject) => {
      if (!navigator.geolocation) {
        reject(new Error('Geolocation is not supported'));
        return;
      }

      const options = {
        enableHighAccuracy: true,
        timeout: 20000, // Increased timeout for better accuracy
        maximumAge: 0 // Always get fresh location
      };

      let attempts = 0;
      const maxAttempts = 3;

      const tryGetLocation = () => {
        attempts++;
        
        navigator.geolocation.getCurrentPosition(
          (position) => {
            const accuracy = position.coords.accuracy;
            console.log(`Location attempt ${attempts}: accuracy ${accuracy}m`);
            
            // If accuracy is good enough (< 50m) or we've tried enough times
            if (accuracy <= 50 || attempts >= maxAttempts) {
              resolve({
                lat: position.coords.latitude,
                lng: position.coords.longitude,
                accuracy: accuracy,
                timestamp: position.timestamp
              });
            } else {
              // Try again for better accuracy
              setTimeout(tryGetLocation, 2000);
            }
          },
          (error) => {
            console.error(`Location attempt ${attempts} failed:`, error);
            if (attempts >= maxAttempts) {
              reject(error);
            } else {
              // Try again with slightly different settings
              setTimeout(tryGetLocation, 2000);
            }
          },
          options
        );
      };

      tryGetLocation();
    });
  };

  // Get current location when component mounts with enhanced accuracy
  useEffect(() => {
    if (navigator.geolocation && !currentLocation) {
      setIsLoadingLocation(true);
      
      getHighAccuracyLocation()
        .then((location) => {
          setCurrentLocation(location);
          setIsLoadingLocation(false);
          
          // If no specific location provided, set current location as selected
          if (!latitude && !longitude && onLocationSelect) {
            setSelectedLocation(location);
            onLocationSelect(location);
          }
        })
        .catch((error) => {
          console.error('Error getting high accuracy location:', error);
          setIsLoadingLocation(false);
          
          // Fallback to standard geolocation
          navigator.geolocation.getCurrentPosition(
            (position) => {
              const location = {
                lat: position.coords.latitude,
                lng: position.coords.longitude,
                accuracy: position.coords.accuracy,
                timestamp: position.timestamp
              };
              setCurrentLocation(location);
              
              if (!latitude && !longitude && onLocationSelect) {
                setSelectedLocation(location);
                onLocationSelect(location);
              }
            },
            (fallbackError) => {
              console.error('Fallback location also failed:', fallbackError);
              // Final fallback to Cairo coordinates
              const fallback = { lat: 30.0444, lng: 31.2357, accuracy: 10000, timestamp: Date.now() };
              setCurrentLocation(fallback);
              if (!latitude && !longitude && onLocationSelect) {
                setSelectedLocation(fallback);
                onLocationSelect(fallback);
              }
            },
            {
              enableHighAccuracy: false, // Fallback with less accuracy but faster
              timeout: 10000,
              maximumAge: 60000
            }
          );
        });
    }
  }, [latitude, longitude, onLocationSelect, currentLocation]);

  // Wait for Google Maps to load with better error handling
  useEffect(() => {
    let mounted = true;
    
    const initializeGoogleMaps = async () => {
      try {
        await loadGoogleMapsScript();
        if (mounted) {
          setIsMapReady(true);
        }
      } catch (error) {
        console.error('Failed to load Google Maps:', error);
        if (mounted) {
          // Try fallback
          setTimeout(() => {
            if (checkGoogleMapsLoaded() && mounted) {
              setIsMapReady(true);
            }
          }, 3000);
        }
      }
    };

    if (!isMapReady) {
      initializeGoogleMaps();
    }

    return () => {
      mounted = false;
    };
  }, [isMapReady]);

  // Initialize map
  useEffect(() => {
    if (isMapReady && mapRef.current && !map && (currentLocation || latitude)) {
      try {
        const mapCenter = {
          lat: latitude || currentLocation?.lat || 30.0444,
          lng: longitude || currentLocation?.lng || 31.2357
        };

        const newMap = new window.google.maps.Map(mapRef.current, {
          zoom: 18, // Higher zoom for better precision (was 16)
          center: mapCenter,
          mapTypeControl: true,
          streetViewControl: true, // Enable street view for better reference
          fullscreenControl: true,
          zoomControl: true,
          scaleControl: true, // Add scale control for distance reference
          rotateControl: true, // Add rotate control
          gestureHandling: 'greedy',
          mapTypeId: 'hybrid', // Hybrid view for better accuracy reference
          styles: [
            {
              featureType: "poi.business",
              elementType: "labels",
              stylers: [{ visibility: "on" }] // Show POI labels for reference
            }
          ]
        });

        // Add click listener for manual selection
        newMap.addListener('click', (event) => {
          const location = {
            lat: event.latLng.lat(),
            lng: event.latLng.lng()
          };
          setSelectedLocation(location);
          if (onLocationSelect) {
            onLocationSelect(location);
          }
          updateDraggableMarker(newMap, location);
        });

        setMap(newMap);
      } catch (error) {
        console.error('Error initializing map:', error);
      }
    }
  }, [isMapReady, currentLocation, latitude, longitude, map, onLocationSelect]);

  // Update draggable marker
  const updateDraggableMarker = (mapInstance, location) => {
    // Remove existing draggable marker
    if (draggableMarker) {
      draggableMarker.setMap(null);
    }

    if (onLocationSelect) {
      // Create new draggable marker
      const marker = new window.google.maps.Marker({
        position: location,
        map: mapInstance,
        title: "  ",
        draggable: true,
        animation: window.google.maps.Animation.DROP,
        icon: {
          url: 'data:image/svg+xml;base64,' + btoa(`
            <svg width="40" height="40" viewBox="0 0 24 24" fill="#ef4444" xmlns="http://www.w3.org/2000/svg">
              <path d="M12 2C8.13 2 5 5.13 5 9c0 5.25 7 13 7 13s7-7.75 7-13c0-3.87-3.13-7-7-7zm0 9.5c-1.38 0-2.5-1.12-2.5-2.5s1.12-2.5 2.5-2.5 2.5 1.12 2.5 2.5-1.12 2.5-2.5 2.5z"/>
              <circle cx="12" cy="9" r="3" fill="white"/>
            </svg>
          `),
          scaledSize: new window.google.maps.Size(40, 40),
          anchor: new window.google.maps.Point(20, 40)
        }
      });

      // Add drag listener
      marker.addListener('dragend', (event) => {
        const newLocation = {
          lat: event.latLng.lat(),
          lng: event.latLng.lng()
        };
        setSelectedLocation(newLocation);
        if (onLocationSelect) {
          onLocationSelect(newLocation);
        }
      });

      setDraggableMarker(marker);
    }
  };

  // Add markers when map or locations change
  useEffect(() => {
    if (map && window.google) {
      // Clear existing markers (except draggable)
      if (map.markers) {
        map.markers.forEach(marker => marker.setMap(null));
      }
      map.markers = [];

      // Current location marker (blue circle)
      if (currentLocation && showCurrentLocation) {
        const marker = new window.google.maps.Marker({
          position: currentLocation,
          map: map,
          title: " ",
          icon: {
            url: 'data:image/svg+xml;base64,' + btoa(`
              <svg width="20" height="20" viewBox="0 0 24 24" fill="#007cff" xmlns="http://www.w3.org/2000/svg">
                <circle cx="12" cy="12" r="8" fill="#007cff" fill-opacity="0.3"/>
                <circle cx="12" cy="12" r="4" fill="#007cff"/>
                <circle cx="12" cy="12" r="2" fill="white"/>
              </svg>
            `),
            scaledSize: new window.google.maps.Size(20, 20),
            anchor: new window.google.maps.Point(10, 10)
          }
        });
        map.markers.push(marker);
      }

      // Rep location marker (for admin view)
      if (repLocation && showBothLocations) {
        const marker = new window.google.maps.Marker({
          position: { lat: repLocation.latitude, lng: repLocation.longitude },
          map: map,
          title: "   ",
          icon: {
            url: 'data:image/svg+xml;base64,' + btoa(`
              <svg width="32" height="32" viewBox="0 0 24 24" fill="#f59e0b" xmlns="http://www.w3.org/2000/svg">
                <path d="M12 2C8.13 2 5 5.13 5 9c0 5.25 7 13 7 13s7-7.75 7-13c0-3.87-3.13-7-7-7zm0 9.5c-1.38 0-2.5-1.12-2.5-2.5s1.12-2.5 2.5-2.5 2.5 1.12 2.5 2.5-1.12 2.5-2.5 2.5z"/>
                <text x="12" y="8" text-anchor="middle" fill="white" font-size="8" font-weight="bold"></text>
              </svg>
            `),
            scaledSize: new window.google.maps.Size(32, 32),
            anchor: new window.google.maps.Point(16, 32)
          }
        });
        map.markers.push(marker);
      }

      // Static location marker (green - for display only)
      if (latitude && longitude && !onLocationSelect) {
        const marker = new window.google.maps.Marker({
          position: { lat: latitude, lng: longitude },
          map: map,
          title: " ",
          icon: {
            url: 'data:image/svg+xml;base64,' + btoa(`
              <svg width="32" height="32" viewBox="0 0 24 24" fill="#10b981" xmlns="http://www.w3.org/2000/svg">
                <path d="M12 2C8.13 2 5 5.13 5 9c0 5.25 7 13 7 13s7-7.75 7-13c0-3.87-3.13-7-7-7zm0 9.5c-1.38 0-2.5-1.12-2.5-2.5s1.12-2.5 2.5-2.5 2.5 1.12 2.5 2.5-1.12 2.5-2.5 2.5z"/>
                <circle cx="12" cy="9" r="3" fill="white"/>
              </svg>
            `),
            scaledSize: new window.google.maps.Size(32, 32),
            anchor: new window.google.maps.Point(16, 32)
          }
        });
        map.markers.push(marker);
      }

      // Create draggable marker for location selection
      if (onLocationSelect) {
        const markerPosition = selectedLocation || 
                              (latitude && longitude ? { lat: latitude, lng: longitude } : null) ||
                              currentLocation;
        
        if (markerPosition) {
          updateDraggableMarker(map, markerPosition);
        }
      }
    }
  }, [map, currentLocation, selectedLocation, latitude, longitude, repLocation, showBothLocations, showCurrentLocation, onLocationSelect]);

  if (!isMapReady || (!window.google || !window.google.maps)) {
    return (
      <div className="h-80 glass-effect rounded-xl flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-500 mx-auto mb-4"></div>
          <p className="font-bold">  ...</p>
          <p className="text-sm opacity-75"> Google Maps API</p>
          {!window.google && (
            <button 
              onClick={() => window.location.reload()} 
              className="mt-3 px-4 py-2 bg-blue-500 text-white rounded-lg text-sm hover:bg-blue-600 transition-colors"
            >
                
            </button>
          )}
        </div>
      </div>
    );
  }

  if (isLoadingLocation) {
    return (
      <div className="h-80 glass-effect rounded-xl flex items-center justify-center">
        <div className="text-center">
          <div className="animate-pulse">
            <div className="w-12 h-12 bg-blue-500 rounded-full mx-auto mb-4 flex items-center justify-center">
              
            </div>
          </div>
          <p className="font-bold">   ...</p>
          <p className="text-sm opacity-75">   </p>
        </div>
      </div>
    );
  }

  return (
    <div className="w-full rounded-xl overflow-hidden relative">
      <div ref={mapRef} style={{ width: '100%', height: '400px' }} />
      
      {/* Enhanced location control buttons */}
      {onLocationSelect && (
        <div className="absolute top-2 left-2 space-y-2">
          <button
            onClick={() => {
              setIsLoadingLocation(true);
              getHighAccuracyLocation()
                .then((location) => {
                  setCurrentLocation(location);
                  setSelectedLocation(location);
                  if (onLocationSelect) {
                    onLocationSelect(location);
                  }
                  if (map) {
                    map.setCenter(location);
                    map.setZoom(19); // Very high zoom for accuracy
                    updateDraggableMarker(map, location);
                  }
                  setIsLoadingLocation(false);
                })
                .catch((error) => {
                  console.error('Error getting high accuracy location:', error);
                  setIsLoadingLocation(false);
                  alert('     .    GPS   .');
                });
            }}
            className="bg-green-600 hover:bg-green-700 text-white px-3 py-2 rounded-lg text-sm font-medium shadow-lg transition-colors flex items-center gap-2"
            disabled={isLoadingLocation}
          >
            {isLoadingLocation ? (
              <>
                <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
                <span> ...</span>
              </>
            ) : (
              <>
                <span></span>
                <span> </span>
              </>
            )}
          </button>
          
          <button
            onClick={() => {
              if (navigator.geolocation) {
                setIsLoadingLocation(true);
                navigator.geolocation.getCurrentPosition(
                  (position) => {
                    const location = {
                      lat: position.coords.latitude,
                      lng: position.coords.longitude,
                      accuracy: position.coords.accuracy,
                      timestamp: position.timestamp
                    };
                    setCurrentLocation(location);
                    setSelectedLocation(location);
                    if (onLocationSelect) {
                      onLocationSelect(location);
                    }
                    if (map) {
                      map.setCenter(location);
                      updateDraggableMarker(map, location);
                    }
                    setIsLoadingLocation(false);
                  },
                  (error) => {
                    console.error('Error getting location:', error);
                    setIsLoadingLocation(false);
                    alert('    .     .');
                  },
                  {
                    enableHighAccuracy: true,
                    timeout: 15000,
                    maximumAge: 0
                  }
                );
              }
            }}
            className="bg-blue-600 hover:bg-blue-700 text-white px-3 py-2 rounded-lg text-sm font-medium shadow-lg transition-colors flex items-center gap-2"
            disabled={isLoadingLocation}
          >
            {isLoadingLocation ? (
              <>
                <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
                <span> ...</span>
              </>
            ) : (
              <>
                <span></span>
                <span> </span>
              </>
            )}
          </button>
        </div>
      )}
      
      {/* Accuracy indicator */}
      {currentLocation && currentLocation.accuracy && (
        <div className="absolute top-2 right-2 bg-white bg-opacity-90 rounded-lg p-2 text-xs">
          <div className="text-gray-600"> :</div>
          <div className={`font-bold ${
            currentLocation.accuracy <= 10 ? 'text-green-600' :
            currentLocation.accuracy <= 50 ? 'text-yellow-600' :
            'text-red-600'
          }`}>
            {Math.round(currentLocation.accuracy)}
          </div>
          {currentLocation.accuracy > 50 && (
            <div className="text-red-500 text-xs mt-1">  </div>
          )}
        </div>
      )}
      
      {/* Map type controls */}
      <div className="absolute bottom-2 left-2 space-x-2 flex">
        <button
          onClick={() => map && map.setMapTypeId('roadmap')}
          className="bg-white hover:bg-gray-100 px-2 py-1 rounded text-xs shadow"
        >
          
        </button>
        <button
          onClick={() => map && map.setMapTypeId('satellite')}
          className="bg-white hover:bg-gray-100 px-2 py-1 rounded text-xs shadow"
        >
           
        </button>
        <button
          onClick={() => map && map.setMapTypeId('hybrid')}
          className="bg-white hover:bg-gray-100 px-2 py-1 rounded text-xs shadow"
        >
          
        </button>
      </div>
      
      {/* Legend for different markers */}
      {(showCurrentLocation || showBothLocations || onLocationSelect) && (
        <div className="absolute top-2 right-2 bg-white/90 backdrop-blur-sm rounded-lg p-2 text-xs shadow-lg">
          {showCurrentLocation && currentLocation && (
            <div className="flex items-center gap-1 mb-1">
              <div className="w-3 h-3 bg-blue-500 rounded-full"></div>
              <span> </span>
            </div>
          )}
          {onLocationSelect && (
            <div className="flex items-center gap-1 mb-1">
              <div className="w-3 h-3 bg-red-500 rounded-full"></div>
              <span>  ( )</span>
            </div>
          )}
          {showBothLocations && repLocation && (
            <div className="flex items-center gap-1">
              <div className="w-3 h-3 bg-amber-500 rounded-full"></div>
              <span> </span>
            </div>
          )}
        </div>
      )}
      
      {/* Instructions */}
      {onLocationSelect && (
        <div className="absolute bottom-2 left-2 right-2">
          <div className="bg-blue-600/90 backdrop-blur-sm text-white rounded-lg p-2 text-xs text-center">
                    
          </div>
        </div>
      )}
    </div>
  );
};

