          clinic_name: language === 'ar' ? ' ' : 'Al Amal Clinic',
          doctor_name: language === 'ar' ? '.  ' : 'Dr. Mohamed Ibrahim',
          sales_rep_name: language === 'ar' ? ' ' : 'Fatema Mohamed',
          visit_goals: language === 'ar' ? '  ' : 'Initial introduction visit',
          location: language === 'ar' ? '   ' : 'Saad Zaghloul Street, Mansoura',
          status: 'missed',
          effectiveness: false,
          has_voice_notes: true,
