// Admin Feature Control Component
const AdminFeatureControl = () => {
  const [features, setFeatures] = useState({});
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchFeatures();
  }, []);

  const fetchFeatures = async () => {
    try {
      const token = localStorage.getItem('access_token');
      const response = await axios.get(`${API}/admin/features/status`, {
        headers: { Authorization: `Bearer ${token}` }
      });
      setFeatures(response.data);
    } catch (error) {
      console.error('Error fetching features:', error);
    } finally {
      setLoading(false);
    }
  };

  const toggleFeature = async (featureName, enabled) => {
    try {
      const token = localStorage.getItem('access_token');
      await axios.post(`${API}/admin/features/toggle`, {
        feature_name: featureName,
        enabled: enabled
      }, {
        headers: { Authorization: `Bearer ${token}` }
      });
      
      setFeatures(prev => ({
        ...prev,
        [featureName]: enabled
      }));
    } catch (error) {
      console.error('Error toggling feature:', error);
    }
  };

  const featureLabels = {
    gps_tracking: ' GPS',
    gamification: ' ',
    chat_system: ' ',
    document_scanner: ' ',
    notifications: ' ',
    advanced_reports: ' ',
    voice_notes: ' ',
    offline_sync: '  ',
    qr_codes: ' QR',
    multi_language: ' ',
    accounting_system: ' '
  };

  if (loading) {
    return (
      <div className="p-8 text-center">
        <div className="loading-spinner-enhanced mx-auto mb-4"></div>
        <p>   ...</p>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="glass-effect p-6 rounded-xl">
        <h3 className="text-xl font-bold mb-4 flex items-center gap-2">
          <span></span>
          <span> </span>
        </h3>
        <p className="text-sm mb-6" style={{ color: 'var(--text-secondary)' }}>
                     
        </p>
        
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {Object.entries(features).map(([feature, enabled]) => (
            <div key={feature} className="p-4 bg-white bg-opacity-5 rounded-lg">
              <div className="flex items-center justify-between mb-2">
                <span className="font-medium">{featureLabels[feature] || feature}</span>
                <label className="relative inline-flex items-center cursor-pointer">
                  <input
                    type="checkbox"
                    checked={enabled}
                    onChange={(e) => toggleFeature(feature, e.target.checked)}
                    className="sr-only"
                  />
                  <div className={`w-11 h-6 rounded-full transition-colors ${enabled ? 'bg-blue-600' : 'bg-gray-300'} relative`}>
                    <div className={`w-5 h-5 bg-white rounded-full shadow transform transition-transform ${enabled ? 'translate-x-5' : 'translate-x-0'} absolute top-0.5 left-0.5`}></div>
                  </div>
                </label>
              </div>
              <div className="text-xs" style={{ color: 'var(--text-muted)' }}>
                {enabled ? ' ' : '  '}
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

