    orderId: ' ',
    clinic: '',
    salesRep: '',
    warehouse: '',
    items: ' ',
    total: '',
    status: '',
    actions: '',
    pendingManager: '  ',
    pendingAccounting: '  ',
    pendingWarehouse: '  ',
    approve: '',
    reject: '',
    view: ''
