    total: 'Total',
    status: 'Status',
    actions: 'Actions',
    pendingManager: 'Pending Manager',
    pendingAccounting: 'Pending Accounting',
    pendingWarehouse: 'Pending Warehouse',
    approve: 'Approve',
    reject: 'Reject',
    view: 'View'
