// Invoice Create Modal Component
const InvoiceCreateModal = ({ onClose, onSave }) => {
  const { t } = useLanguage();
  const [formData, setFormData] = useState({
    customer_name: '',
    customer_specialty: '',
    clinic_name: '',
    discount_amount: 0,
    items: [{ product_name: '', quantity: 1, unit_price: 0 }]
  });

  const addItem = () => {
    setFormData({
      ...formData,
      items: [...formData.items, { product_name: '', quantity: 1, unit_price: 0 }]
    });
  };

  const removeItem = (index) => {
    const newItems = formData.items.filter((_, i) => i !== index);
    setFormData({ ...formData, items: newItems });
  };

  const updateItem = (index, field, value) => {
    const newItems = [...formData.items];
    newItems[index] = { ...newItems[index], [field]: value };
    setFormData({ ...formData, items: newItems });
  };

  const calculateTotals = () => {
    const subtotal = formData.items.reduce((sum, item) => 
      sum + (item.quantity * item.unit_price), 0
    );
    const discountAmount = formData.discount_amount || 0;
    const taxAmount = (subtotal - discountAmount) * 0.15;
    const total = subtotal - discountAmount + taxAmount;

    return { subtotal, discountAmount, taxAmount, total };
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const totals = calculateTotals();
    
    const newInvoice = {
      ...formData,
      id: `INV-${Date.now()}`,
      invoice_number: `INV-${Date.now()}`,
      created_at: new Date().toISOString(),
      subtotal: totals.subtotal,
      tax_amount: totals.taxAmount,
      total_amount: totals.total,
      status: 'pending'
    };

    // Add total_price to each item
    newInvoice.items = newInvoice.items.map(item => ({
      ...item,
      total_price: item.quantity * item.unit_price
    }));

    onSave(newInvoice);
  };

  const totals = calculateTotals();

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="modal-modern p-6 w-full max-w-4xl max-h-[95vh] overflow-y-auto">
        <h3 className="text-2xl font-bold mb-6 text-gradient">  </h3>
        
        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Customer Information */}
          <div className="glass-effect p-6 rounded-xl">
            <h4 className="text-lg font-bold mb-4"> </h4>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div>
                <label className="block text-sm font-bold mb-2"> :</label>
                <input
                  type="text"
                  value={formData.customer_name}
                  onChange={(e) => setFormData({...formData, customer_name: e.target.value})}
                  className="form-modern w-full"
                  required
                />
              </div>
              <div>
                <label className="block text-sm font-bold mb-2">:</label>
                <input
                  type="text"
                  value={formData.customer_specialty}
                  onChange={(e) => setFormData({...formData, customer_specialty: e.target.value})}
                  className="form-modern w-full"
                  required
                />
              </div>
              <div>
                <label className="block text-sm font-bold mb-2"> :</label>
                <input
                  type="text"
                  value={formData.clinic_name}
                  onChange={(e) => setFormData({...formData, clinic_name: e.target.value})}
                  className="form-modern w-full"
                  required
                />
              </div>
            </div>
          </div>

          {/* Items */}
          <div className="glass-effect p-6 rounded-xl">
            <div className="flex items-center justify-between mb-4">
              <h4 className="text-lg font-bold"> </h4>
              <button
                type="button"
                onClick={addItem}
                className="btn-primary flex items-center gap-2"
              >
                <SVGIcon name="add" size={20} />
                <span>{t('add')} {t('order')}</span>
              </button>
            </div>

            <div className="space-y-4">
              {formData.items.map((item, index) => (
                <div key={index} className="grid grid-cols-1 md:grid-cols-4 gap-4 p-4 border rounded-lg">
                  <div>
                    <label className="block text-sm font-bold mb-2">:</label>
                    <input
                      type="text"
                      value={item.product_name}
                      onChange={(e) => updateItem(index, 'product_name', e.target.value)}
                      className="form-modern w-full"
                      required
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-bold mb-2">:</label>
                    <input
                      type="number"
                      value={item.quantity}
                      onChange={(e) => updateItem(index, 'quantity', parseInt(e.target.value))}
                      className="form-modern w-full"
                      min="1"
                      required
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-bold mb-2"> :</label>
                    <input
                      type="number"
                      value={item.unit_price}
                      onChange={(e) => updateItem(index, 'unit_price', parseFloat(e.target.value))}
                      className="form-modern w-full"
                      step="0.01"
                      min="0"
                      required
                    />
                  </div>
                  <div className="flex items-end">
                    <button
                      type="button"
                      onClick={() => removeItem(index)}
                      className="btn-danger w-full"
                      disabled={formData.items.length === 1}
                    >
                      
                    </button>
                  </div>
                </div>
              ))}
            </div>
          </div>

          {/* Discount */}
          <div className="glass-effect p-6 rounded-xl">
            <h4 className="text-lg font-bold mb-4"></h4>
            <div className="w-1/3">
              <label className="block text-sm font-bold mb-2"> :</label>
              <input
                type="number"
                value={formData.discount_amount}
                onChange={(e) => setFormData({...formData, discount_amount: parseFloat(e.target.value) || 0})}
                className="form-modern w-full"
                step="0.01"
                min="0"
              />
            </div>
          </div>

          {/* Totals Summary */}
          <div className="glass-effect p-6 rounded-xl">
            <h4 className="text-lg font-bold mb-4"> </h4>
            <div className="w-1/2 space-y-2">
              <div className="flex justify-between">
                <span> :</span>
                <span>{totals.subtotal.toFixed(2)} .</span>
              </div>
              {totals.discountAmount > 0 && (
                <div className="flex justify-between text-red-600">
                  <span>:</span>
                  <span>-{totals.discountAmount.toFixed(2)} .</span>
                </div>
              )}
              <div className="flex justify-between">
                <span> (15%):</span>
                <span>{totals.taxAmount.toFixed(2)} .</span>
              </div>
              <div className="border-t pt-2 flex justify-between text-lg font-bold">
                <span>:</span>
                <span className="text-green-600">{totals.total.toFixed(2)} .</span>
              </div>
            </div>
          </div>

          {/* Actions */}
          <div className="flex gap-4 pt-4">
            <button type="submit" className="btn-primary flex-1">
               
            </button>
            <button type="button" onClick={onClose} className="btn-secondary flex-1">
              
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

