// Enhanced User Management Component with Performance Cards
const AdvancedUserManagement = () => {
  const [users, setUsers] = useState([]);
  const [selectedUser, setSelectedUser] = useState(null);
  const [showUserCard, setShowUserCard] = useState(false);
  const [loading, setLoading] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');
  const { language, t, isRTL } = useLanguage();

  useEffect(() => {
    loadEnhancedUsers();
  }, []);

  const loadEnhancedUsers = async () => {
    setLoading(true);
    try {
      const token = localStorage.getItem('access_token');
      const response = await axios.get(`${API}/users`, {
        headers: { Authorization: `Bearer ${token}` }
      });
      setUsers(response.data || []);
    } catch (error) {
      console.error('Error loading enhanced users:', error);
    } finally {
      setLoading(false);
    }
  };

  const viewUserPerformance = async (user) => {
    try {
      const token = localStorage.getItem('access_token');
      const response = await axios.get(`${API}/users/${user.id}/performance`, {
        headers: { Authorization: `Bearer ${token}` }
      });
      
      setSelectedUser({
        ...user,
        performanceData: response.data
      });
      setShowUserCard(true);
    } catch (error) {
      console.error('Error loading user performance:', error);
    }
  };

  const filteredUsers = users.filter(user =>
    user.full_name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    user.username?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    user.role?.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const getRoleLabel = (role) => {
    const roleLabels = {
      'admin': ' ',
      'gm': ' ',
      'line_manager': ' ',
      'area_manager': ' ',
      'district_manager': ' ',
      'key_account': ' ',
      'medical_rep': ' ',
      'warehouse_keeper': ' ',
      'accounting': ''
    };
    return roleLabels[role] || role;
  };

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="text-center mb-8">
        <h1 className="text-3xl font-bold mb-2">   </h1>
        <p className="text-sm" style={{ color: 'var(--text-secondary)' }}>
                 
        </p>
      </div>

      {/* Search Bar */}
      <div className="mb-6">
        <div className="relative">
          <input
            type="text"
            placeholder="  ..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="input-glass w-full pl-12"
          />
          <span className="absolute left-4 top-1/2 transform -translate-y-1/2 text-gray-400">
            
          </span>
        </div>
      </div>

      {/* Users Grid */}
      {loading ? (
        <div className="text-center py-8">
          <div className="animate-spin inline-block w-8 h-8 border-4 border-blue-500 border-t-transparent rounded-full"></div>
          <p className="mt-2"> ...</p>
        </div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredUsers.map((user) => (
            <UserPerformanceCard
              key={user.id}
              user={user}
              onViewPerformance={() => viewUserPerformance(user)}
              getRoleLabel={getRoleLabel}
            />
          ))}
        </div>
      )}

      {/* User Performance Modal */}
      {showUserCard && selectedUser && (
        <UserDetailedProfile
          user={selectedUser}
          onClose={() => setShowUserCard(false)}
          getRoleLabel={getRoleLabel}
        />
      )}
    </div>
  );
};

